import * as moment from 'moment';
import { findIndex, join } from 'lodash';

const zenGin = 'ZenGin Archive';
const genericArchive = 'zCArchiverGeneric';
const ASCII = 'ASCII';
const END = 'END';
const gothicDateFormat = 'DD.M.YYYY HH:mm:ss';

const somethingWrongMessage = (field: string): string => (
  `Co jest nie tak z wartoci "${field}"`
);
const errorMessages = [
  'To nie jest archiwum ZenGin',
  somethingWrongMessage('ver'),
  'Plik musi by zapisany w formacie ASCII, przekonwertuj wiat w programie Spacer',
  'Plik musi by zapisany w formacie ASCII, przekonwertuj wiat w programie Spacer',
  somethingWrongMessage('saveGame'),
  somethingWrongMessage('date'),
  somethingWrongMessage('user'),
  'Niewaciwy format pliku',
  somethingWrongMessage('objects'),
  'Niewaciwy format pliku',
  'Niewaciwy format pliku',
];

export class PrefixZenData {
  ver: string;
  saveGame: string;
  date: moment.Moment;
  user: string;
  objects: string;

  isValid = true;
  error: string;

  constructor(prefix: Array<string>) {

    const converters: Array<(line: string) => boolean> = [
      (line) => line !== zenGin,
      (line) => {
        const match = line.match(/(ver )(\d+\s*)/);
        if (match) {
          this.ver = match[2];
        } else { return true; }
      },
      (line) => line !== genericArchive,
      (line) => line !== ASCII,
      (line) => {
        const match = line.match(/(saveGame )(\d+\s*)/);
        if (match) {
          this.saveGame = match[2];
        } else { return true; }
      },
      (line) => {
        const match = line.match(/(date )(.+)/);
        if (match) {
          const date = moment(match[2], gothicDateFormat);
          if (date.isValid) {
            this.date = date;
          } else { return true; }
        } else { return true; }
      },
      (line) => {
        const match = line.match(/(user )(.+)/);
        if (match) {
          this.user = match[2];
        } else { return true; }
      },
      (line) => line !== END,
      (line) => {
        const match = line.match(/(objects )(\d+\s*)/);
        if (match) {
          this.objects = match[2];
        } else { return true; }
      },
      (line) => line !== END,
      (line) => line !== '',
    ];

    if (prefix.length !== 11) {
      this.setError(errorMessages[0]);
    } else {
      const errorIndex = findIndex(converters, (condition, i) => condition(prefix[i]));
      if (errorIndex !== -1) { this.setError(errorMessages[errorIndex]); }
    }
  }

  private setError(message: string) {
    this.isValid = false;
    this.error = message;
  }

  public toString() {
    return join([
      zenGin,
      `ver ${this.ver}`,
      genericArchive,
      ASCII,
      `saveGame ${this.saveGame}`,
      `date ${this.date.format(gothicDateFormat)}`,
      `user ${this.user}`,
      END,
      `objects ${this.objects}`,
      END,
      '\n',
    ], '\n');
  }
}
